import { createSlice } from '@reduxjs/toolkit';

let user = createSlice({
  name: 'user',
  initialState: { name: '', age: 2 },
  reducers: {
    changeName(prev) {
      prev.name = ' VVIP ';
    },
    changeAge(prev, action) {
      prev.age += action.payload;
    },
  },
});

export let { changeName, changeAge } = user.actions;
export default user;
